<template>
  <!--我的-->
  <div class="mine-body">
    <div class="mine-header">
      <a-breadcrumb separator=">">
        <a-breadcrumb-item>我的</a-breadcrumb-item>
        <a-breadcrumb-item>{{ this.$route.meta.title }}</a-breadcrumb-item>
      </a-breadcrumb>
      <hr />
    </div>
    <div class="mine-section">
      <span>{{ this.$route.meta.title }}</span>
      <div>
        <span>工厂名称︰</span>
        <span>{{ mineitem.merName }}</span>
      </div>
      <div>
        <span>负责人：</span>
        <span>数据</span>
      </div>

      <div>
        <span>商标类型：</span>
        <span>{{ mineitem.trademarkType }}</span>
      </div>
      <div>
        <span>入驻类型：</span>
        <span>{{ mineitem.businessOperation }}</span>
      </div>
      <div>
        <a-button type="link" href="/minelist">查看详情</a-button>
        <a-button type="link" href="/change">更改密码</a-button>
      </div>
    </div>
    <div class="mine-footer">
      <span>我的襄理 <a-button type="primary">工单列表</a-button></span>
      <div>
        <span>当前无襄理</span>
        <a-button type="primary"> 去选择</a-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      mineitem: [],
    };
  },
  mounted() {
    this.mine();
  },
  methods: {
    async mine() {
      let str = {
        id: sessionStorage.getItem("id"),
      };
      let { data } = await this.$http.getmer(str);
      console.log(data);
      if (data.code == "0000") {
        this.mineitem = data.data.mer.merInfo;
        this.status();
      }
    },
    status() {
      switch (this.mineitem.trademarkType) {
        case 0:
          this.mineitem.trademarkType = "自由品牌";
          break;
        case 1:
          this.mineitem.trademarkType = "共有品牌";
          break;
        case 2:
          this.mineitem.trademarkType = "加盟蓝天";
          break;
      }
    },
  },
};
</script>

<style scoped>
.mine-section {
  padding: 0px 150px;
}
.mine-section div {
  border-top: 0;
  border: 1px solid #c0c0c0;
  padding: 10px 10px 10px 180px;
}
.mine-section > :nth-child(1) {
  width: 100%;
  height: 40px;
  line-height: 40px;
  background: #c0c0c0;
  font-size: 15px;
  font-weight: 900;
  display: block;
}
.mine-section div > :nth-child(1) {
  width: 10%;
  display: inline-block;
  text-align: right;
}
.mine-section div > :nth-child(2) {
  width: 90%;
  display: inline-block;
  text-align: left;
}
.mine-section > :nth-child(6){
  text-align: right !important;
}
.mine-section > :nth-child(6)>:nth-child(1){
   display: inline !important;
}
.mine-section > :nth-child(6)>:nth-child(2){
   display: inline !important;
}
.mine-footer {
  padding: 50px 150px;
}
.mine-footer div {
  border: 1px solid #c0c0c0;
  border-top: 0px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 50px 0px 30px;
  height: 50px;
  font-size: 16px;
}
.mine-footer > :nth-child(1) {
  display: block;
  background: #c0c0c0;
  height: 50px;
  line-height: 50px;
  font-size: 16px;
  font-weight: 900;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 50px;
}
</style>